// @flow

import React from 'react';
import QA from './QA';

const qas = [
  ['Where to contact for bug reports, feature requests, etc. I appreciate all tips and advice regarding any aspect of this site as well.', 'lolonetricks@gmail.com'],
  ['Why isnt this 100% accurate? So and so isnt on here! So and so is on here, but was kicked out! This person isn\'t masters, this person isn\'t challenger, etcetc.', 'Itd be tough to make it perfectly accurate. I simply wanted to give everyone an accurate enough representation of the current one tricks in challengers and masters. Someone should be able to go on the site and get a good enough idea of what champions are being one tricked into the highest level of play.\nIm still a student in college, and Im still new to making complete projects. I simply went with the easiest route (when analyzing the data) and deployed ASAP.\nOver the next few weeks, Ill improve it so that we get more up-to-date data though!'],
  ['This person isnt a 1 trick!', 'Over the next few weeks, Ill be making drastic changes to the backend as I have more time. I\'m working with a few other people to create a more robust algorithm. Although there are slight twists to the phrase one trick pony today, I define it as someone who really excels on one champ, but does below average on other champs. These types of one tricks are much more prominent in higher elo, and I found that a majority of them are easily identifiable and thus are on this list.'],
  ['The na.op.gg link doesnt work.', 'They changed their name. Try the lolking link instead (lolking will redirect you to their new name). The database will correct the names some time overnight.'],
  ['What youd make this with?', 'HTML, CSS, JS, React, Express, and MongoDB.'],
  ['Any special mentions?', 'I\'d like to thank Richelle for all the support she has provided me, and Richelle if you\'re reading this, I hope you stop being a hardstuck D5* player. I\'d like to thank all the people who checked out my site in its early stages and provided feedback and encouragement.'],
];

const FAQ = (): React$Element<any> =>
  <div className="faq">
    <h3 className="faq-header">faq</h3>
    <div className="qa">
      { qas.map((item, index) => <QA key={index} question={item[0]} answer={item[1]} />) }
    </div>
  </div>;

export default FAQ;



